<script>
import { mapActions, mapMutations } from 'vuex'
import * as types from '../../store/mutationType'
import { setCookie } from '../../utils/util';
import router from '../../router';

export default {
  name: "login",
  data () {
    return {
      loginForm: {
        phone: '',
        password: ''
      },
      loginRules: {
        phone: [{ required: true, message: '请输入手机号哦', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码哦', trigger: 'blur' }]
      },
      loginLoading: false,
      pic: require('../../assets/lgbg.png')
    }
  },
  methods: {
    ...mapMutations({
      toSetUserInfo: types.SET_USER_INFO
    }),
    ...mapActions(['login']),
    // 提交登录
    handleSubmit (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.loginLoading = true
          this.login(this.loginForm)
              .then(res => {
                this.toSetUserInfo(res.profile)
                this.loginLoading = false
                this.$message.success('登录成功')
                window.localStorage.setItem('userInfo', JSON.stringify(res.profile))
                setCookie(res.cookie)
                this.$router.go(-1)
                // this.$router.replace('/')
              })
              .catch(err => {
                this.loginLoading = false
                if (err.code === 400) {
                  this.$message.error('请输入正确格式的手机号')
                } else if (err.code === 502) {
                  this.$message.error('手机号或密码错误')
                } else if (err.data.code === 501) {
                  this.$message.error('该手机号尚未注册')
                } else if (err.data.code === 509) { // 之所以会有这样的差异，是因为，400和502的status是200，501的status是501，509的status是509
                  this.$message.error('密码错误超过限制，请稍后再试')
                } else {
                  this.$message.error('似乎出了什么问题')
                }
              })
        }
      })
    }
  },
  mounted () {
    this.$notify.warning({
      title: '请不要禁用cookie哦',
      message: '如果您的浏览器禁用了cookie，会导致登录以及用户相关操作不正常'
    });
  }
}
</script>

